video{
    object-fit: cover;
    width:100%;
    height: 100%;
    position: fixed;
    z-index: -1;

}

.hero-container{
    background: url('/public/images/img-home.jpeg') center center/cover no-repeat;
    height: 125vh;
    width:100%;
    display: flex;
    flex-direction:column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;


}
.hero-container > h1 {

    color: #fff;
    font-size: 80px;
    margin-top: 8px;


}



.hero-container > p{
    color:#fff;
    margin: 8px;
    font-size:32px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 
    'Lucida Grande', 'Lucida Sans', 
    Arial, sans-serif

}

.hero-btns{
    margin-top:32px;

}

.hero-btns .btn{
    margin:6px;

}

.fas fa-play-circle{

    margin-left: 4px;
}

@media screen and (max-width:960px){
    .hero-container > h1 {

     
        font-size: 100px;
        margin-top: -10px; 
    
    
    }

}

@media screen and (max-width:768px){
    .hero-container > h1 {

     
        font-size: 50px;
        margin-top: 3px;
    
    }
    .hero-container > p{
        font-size: 30px;
    }

    .hero-mobile{
        display: block;
        text-decoration: none;
    }
    .btn{
        
        width:100%;

    }
}